	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p>A book dedicated to the avid do-it-yourself enthusiast, Upcycle aims to reduce waste by challenging viewers to explore ways to transform old household products to new again. By strictly using illustrations and typography, the look of the final product is up to the viewers imagination and creativity.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.64999999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"null\"\n        title=\"\"\n        src=\"/static/_p0i5146-00b5c04e1e4b250da5d0d0fc861bdb8d-35578.jpg\"\n        srcset=\"/static/_p0i5146-00b5c04e1e4b250da5d0d0fc861bdb8d-21994.jpg 375w,\n/static/_p0i5146-00b5c04e1e4b250da5d0d0fc861bdb8d-f7c9d.jpg 750w,\n/static/_p0i5146-00b5c04e1e4b250da5d0d0fc861bdb8d-35578.jpg 1500w,\n/static/_p0i5146-00b5c04e1e4b250da5d0d0fc861bdb8d-5ea4e.jpg 2000w\"\n        sizes=\"(max-width: 1500px) 100vw, 1500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>Something that inspires me as a designer is taking an idea and creating something completely new. By making something new again it is not about replacing the old, but simply improving to make it better or increase its value. Upcycle is a book dedicated to those who seek this passion as well as those who do not like to see good things go to waste. By completing these tasks you get the chance to give something a second life.</p>","frontmatter":{"title":"Upcycle","graduate":"Jennifer Floto","category":"Book Design","category2":null,"category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/static/floto_jennifer(5)_digital_002-50559edcdd6b899ce28e0dd8ff19f3cf-74fdd.jpg","srcSet":"/static/floto_jennifer(5)_digital_002-50559edcdd6b899ce28e0dd8ff19f3cf-9c43b.jpg 375w,\n/static/floto_jennifer(5)_digital_002-50559edcdd6b899ce28e0dd8ff19f3cf-c109f.jpg 750w,\n/static/floto_jennifer(5)_digital_002-50559edcdd6b899ce28e0dd8ff19f3cf-74fdd.jpg 1333w","sizes":"(max-width: 1333px) 100vw, 1333px"}}}}}},"pathContext":{"slug":"/work/upcycle/","name":"/Upcycle/"}}

